.team{



  .t-wrapper{
    display : flex;
    flex-direction : column;
    margin: 2%;

    .head{
      font-size : 25px;
      color : #48ACF0;
      font-weight : bold;
      text-align : center;


    }

    .team-info{
      display : flex;
      gap : 30px;
      padding : 10px;
      margin: 2% 10%;

      .image-side{
        display :flex;
        flex-direction : column;
        align-items : center;
        align-content :center;
        color : #48ACF0;
        font-weight : bold;
        gap : 10px;

        .member-photo{
          width: 250px;
          height: 300px;
          border-radius : 10px;
          object-fit: cover;

        }

        .member-name{

        }
      }

      .text-side{
        display: flex;
        align-content : center;
        font-size : 30px;
        font-weight : bold;
        color : #48ACF0;
        width : 70%;


      }

    }

  }

}