.compare{
  color : #48ACF0;
  font-weight: bold;
  margin-bottom:  2%;
  margin-top: 2%;

  .compare-wrapper{
    padding: 10px;
    box-sizing: border-box;
    display:flex;
    flex-direction: column;

    .head{
      text-align: center;
      font-size : 30px;

    }

    .compare-fields{
      display: flex;
      gap : 40px;
      margin : 2% 30%;


      .input{
        padding: 10px;
        height : 30px;
        width : 200px;
        border-radius : 20px;
        border: none;
        outline : none;
        background-color: #48ACF0;

        &::placeholder{
          text-align: center;
          color :white;
          font-weight:bold;
        }

      }
    }

    .compare-btn{
      color :white;
      text-align: center;

      .compare-sect{
       border-radius: 15px;
        cursor: pointer;
        padding: 15px;
        background-color: #48ACF0;
        display: inline-block;

      }
    }

    .compares{
      text-align: center;

      .item-c{
        margin-top: 1.5%;
        margin-left : 4%;

        &.green{
          color : #82ca9d;
        }
      }
    }
  }
}