.a-hero{
  margin :0;
  padding :0;
  box-sizing: border-box;
  font-family: Montserrat,Nunito,serif;
  //background-image: url("https://images.pexels.com/photos/2170387/pexels-photo-2170387.jpeg?auto=compress&cs=tinysrgb&w=600");
  background-image: url("https://images.pexels.com/photos/3813341/pexels-photo-3813341.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 95vh;
  //width: 100vw;
  color: white;
  overflow-x: hidden;


  .a-hero-wrapper{

    .head{
      font-size : 80px;
      font-weight:800;
      text-align: center;

    }
    .about-text{
      font-size : 30px;
      margin : 3% 17%;
      font-weight: bold;

    }

    .email{
      cursor: pointer;


      .email-item{
        text-align: center;
        margin: 0 40% 0 42%;
        display: inline-block;
        background-color :#48ACF0;;
        color :white;
        font-weight: bold;
        padding : 12px;
        border :none;
        border-radius :15px;
        font-size : 20px;
        &:hover{
          color:#48ACF0;
          background-color:white;
        }



      }
    }


  }
}