.hero{
  margin :0;
  padding :0;
  box-sizing: border-box;
  font-family: Montserrat,Nunito,serif;
  background-image: url("https://images.pexels.com/photos/3767406/pexels-photo-3767406.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
  //width: 100vw;
  color: white;
  overflow-x: hidden;
  .heroWrapper{
    box-sizing: border-box;

    .text{
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-top : 5%;
      height: 100%;



      .section{
        display: flex;
        flex-direction:column;
        align-items:center;


        .big-text{
          margin-bottom: 10%;


          .h1{
            font-size : 80px;
            font-weight:800;

            .change{
              color:#48ACF0;
            }

          }

          .stext{
            font-style: italic;
            margin-left: 15%;
            font-size:20px;
            font-weight:bold;


          }
        }

        .discover{
          margin-top : 10%;
          cursor: pointer;
          margin-left :12%;




          .dtext{
            font-weight: bold;
            color:white;
            padding: 15px 30px;
            background-color: #48ACF0;
            border-radius : 15px;
            text-align: center;
            font-size: 20px;
            text-decoration: none;


            &:hover{
              scale: 2.5%;
              color:#48ACF0;
              background-color:white;
            }


          }
        }

      }

    }


  }


}